@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica&display=swap);
body {
	background-color: #e9e5e3;
}

/* Custom scrollbar */
* {
	scrollbar-width: thin;
	scrollbar-color: #aab1b3 #e9e5e3;
}

*::-webkit-scrollbar {
	width: 15px;
}
  
*::-webkit-scrollbar-track {
	background: #e9e5e3;        /* color of the tracking area */
}
  
*::-webkit-scrollbar-thumb {
	background-color: #aab1b3;    /* color of the scroll thumb */
	border-radius: 100px;       /* roundness of the scroll thumb */
}
.font-title {
	font-family: 'IM Fell DW Pica', serif;
}

.font-subtitle {
	font-family: 'Julius Sans One', sans-serif;
}

.font-main {
	font-family: 'Open Sans Condensed', sans-serif;
}

.bg-offwhite {
	background-color: #e9e5e3;
}

.bg-light-bluegray {
	background-color: #aab1b3;
}

.bg-lighttan {
	background-color: #c09e72;
}

.bg-darktan {
	background-color: #9e6228;
}

.fc-offwhite {
	color: #e9e5e3;
}

.fc-light-bluegray {
	color: #aab1b3;
}

.fc-dark-bluegray {
	color: #394545;
}

.fc-lighttan {
	color: #c09e72;
}

.fc-darktan {
	color: #9e6228;
}

a {
	color: #394545;
}

a:visited {
	color: #394545;
}
a.headerNavLink {
  text-decoration: none;
}

a.headerNavLink:hover {
	color: #e9e5e3;
	transition: .5s;
}

a.activeRoute {
  color: #9e6228;
}

.header-background {
	background-image: url(/static/media/ep_naturalwhite.5ab80fbd.png);
}
.project-card {
	background-image: url(/static/media/dust_scratches.3a1926a3.png);
	background-size: cover;
	height: 300px;
	display: grid;
	grid-template-rows: 10% 45% 30% 15%;
}

@media only screen and (max-width: 90em) {
	.project-card {
		height: 400px;
	}
}

.justify-evenly {
	justify-content: space-evenly;
}
